/* src/ClientForm.css */

.client-form-container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #F5F5F5; /* Sekundarna boja */
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.client-form-header h2 {
  color: #006241; /* Primarna boja */
}

.client-form-header img {
  max-width: 150px;
}

.client-form-header h2 {
  color: #006241; /* Primarna boja */
  margin-top: 10px;
}

.client-form label {
  display: block;
  margin-bottom: 5px;
  color: #333333; /* Akcent boja */
  font-weight: bold;
}

.client-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

.client-form button {
  width: 100%;
  padding: 10px;
  background-color: #006241; /* Primarna boja */
  color: #FFFFFF; /* Bijela boja */
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.client-form button:hover {
  background-color: #004d31; /* Tamnija nijansa zelene */
}

.client-form button:disabled {
  background-color: #a5a5a5;
  cursor: not-allowed;
}
