/* src/SearchByOib.css */

.search-by-oib-container {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background-color: #F5F5F5; /* Sekundarna boja */
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.search-by-oib-container h2 {
  color: #006241; /* Primarna boja */
  text-align: center;
  margin-bottom: 20px;
}
